<template>
  <div>
    <div>{{ title }}</div>
    <TimePicker v-if="range" type="timerange" :readonly="readonly" :format="format" v-model="timeRange" @on-change="timeRangeChange">
    </TimePicker>
    <TimePicker v-else v-model="time" :format="format" @on-change="timeChange">
    </TimePicker>
  </div>
</template>

<script>
import validate from '@/api/validate'

export default {
  data () {
    return {
      uuid: '00000000-0000-0000-0000-0000000000010',
      timeRange: [],
      time: new Date()
    }
  },
  props: {
    title: {
      type: String,
      default: '这里应该是一个询问时间的问题'
    },
    description: {
      type: String,
      default: ''
    },
    range: {
      type: [Boolean, String],
      default: false
    },
    value: {
      type: [Array, String],
      default: () => [new Date(), new Date()]
    },
    format: {
      type: String,
      default: 'HH:mm'
    },
    additionalKeys: {
      type: Array
    },
    validate: {
      type: Boolean
    },
    readonly: {
      type: Boolean
    },
    readonlyData: {
      type: Array,
      default: () => []
    },
    schemaKey: {
      type: String
    },
    schemaType: {
      type: String
    },
    apiType: {
      type: String
    },
    server: {
      type: Boolean,
      require: false
    }
  },
  mounted () {
    if (this.range) {
      this.timeRange = (this.value && this.value.length) ? this.value : [new Date(), new Date()]
      this.$emit('input', {
        schemaKey: this.schemaKey,
        value: this.timeRange,
        first: true
      })
    } else {
      this.time = (this.value && this.value.length) ? this.value[0] : new Date()
      this.$emit('input', {
        schemaKey: this.schemaKey,
        value: this.time,
        first: true
      })
    }
  },
  methods: {
    async timeChange () {
      if (this.validate) {
        const tmp = {}
        tmp[this.schemaKey] = this.time
        const data = await validate(this.schemaType, this.apiType, {
          projectId: this.$route.query.projectId,
          valueJson: JSON.stringify(tmp)
        })

        if (data.response.success === false && data.response.msg) {
          this.$Message.error(data.response.msg)
        } else if (data.response.success === true && data.response.msg) {
          this.$Message.info(data.response.msg)
        }
      }
      this.$emit('input', {
        schemaKey: this.schemaKey,
        value: this.time,
        server: this.server
      })
    },
    async timeRangeChange () {
      if (this.validate) {
        const tmp = {}
        tmp[this.schemaKey] = this.timeRange
        const data = await validate(this.schemaType, this.apiType, {
          projectId: this.$route.query.projectId,
          valueJson: JSON.stringify(tmp)
        })

        if (data.response.success === false && data.response.msg) {
          this.$Message.error(data.response.msg)
        } else if (data.response.success === true && data.response.msg) {
          this.$Message.info(data.response.msg)
        }
      }
      this.$emit('input', {
        schemaKey: this.schemaKey,
        value: this.timeRange,
        server: this.server
      })
    },
    blur () {
      if (this.additionalKeys && this.additionalKeys.length) {
        this.$emit('additional', {
          allowClose: true,
          additionalKeys: this.additionalKeys
        })
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
</style>
